






import { Vue, Prop, Component } from "vue-property-decorator";
const prefixCls = "wu-tabs-item";

@Component
export default class TabsItem extends Vue {
  @Prop()
  private id!: string;

  get className() {
    return [
      `${prefixCls}`,
      {
        //@ts-ignore
        [`${prefixCls}-selected`]: this.id === this.$parent.value
      }
    ];
  }
}
